import {deleteCookie} from "./Utils"

export const gaInit = gaId => {
  document.addEventListener(
    "consent-analytics",
    function (e) {
      if (e.detail.status) {
        gaGrant(gaId)
      } else {
        gaRevoke()
      }
    },
    false
  )
}

